import React, {Component} from 'react';
import AutoComplete from 'material-ui/AutoComplete';

const colors = [
  'мыло',
  'шампунь',
  'продукты',
  'оплата телефона',
  'оплата интернета',
  'одежда',
  'парикмахерская',
  'поездки',  
];

/**
 * `AutoComplete` search text can be implemented as a controlled value,
 * where `searchText` is handled by state in the parent component.
 * That value is reseted with the `onNewRequest` callback.
 */
export default class AutoCompleteExampleControlled extends Component {
  state = {
    searchText: '',
  };

  handleUpdateInput = (searchText) => {
    this.setState({
      searchText: searchText,
    });

  };

  handleNewRequest = () => {
    alert("Вы выбрали " + this.state.searchText);
    this.setState({
      searchText: '',
    });
  };

  render() {
    return (
      <div style={{margin: '50px 20px'}}>
      Новая запись о расходах
        <AutoComplete
          hintText="Статья расходов"
          searchText={this.state.searchText}
          onUpdateInput={this.handleUpdateInput}
          onNewRequest={this.handleNewRequest}
          dataSource={colors}
          filter={(searchText, key) => (key.indexOf(searchText) !== -1)}
          openOnFocus={true}
        />
      </div>
    );
  }
}




